.container-left-primary{
    @include degraded-background($principal, $secundario);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    display: grid;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    .container {
        margin-top: 5rem;
        padding-bottom: 100cqmin;
    }
    @include tablet() {
        .img-container-left {
            grid-column: 2;
            height: max-content;
        }
        .container {
            margin: auto;
            padding: 0
        }
        @include grid(2, 0rem);
        height: 100vh;
    }
}

.container-left-secondary {
    @include degraded-background($secundario, $terciario);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    display: grid;
    gap: 0rem;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    .window-login {
        margin-top: 1rem;
    }
    overflow-y: scroll;
    @include tablet() {
        .img-container-left {
            grid-column: 2;
        }
        .window-login {
            margin: auto;
        }
        @include grid(2, 0rem);
    }
    height: 100vh;
}

.container-left-secondary-register {
    @include degraded-background($secundario, $terciario);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    .div-aux{
        padding: 2rem 0;
    }
    display: grid;
    gap: 0rem;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    @include tablet() {
        .img-container-left {
            grid-column: 2;
        }
        .window-login {
            margin: auto;
        }
        @include grid(2, 0rem);
    }
}

.window-login {
    text-align: center;
    margin: auto;
    width: 80%;
    border-radius: 2rem;
    background-color: $blanco;
    form{
        width: 80%;
        margin: auto;
        display: flex;
        flex-direction: column;
        label{
            text-align: left;
        }
        input{
            width: 100%;
            border-radius: 1rem;
            align-self: center;
            border-color: $terciario;
            margin-bottom: 2rem;
        }
    }
}

.logo-login{
    margin: auto;
    width: 25%;
}