.dashboard{
    width: 90%;
    margin: auto;
}  
.windows{
    @include telefono() {
        @include grid-row(4, 1rem);
        .option-client {
            height: max-content;
            img {
                height: 20rem;
                width: 100%
            }
        }
    }
    @include tablet() {
        @include grid(4, 1rem);
    }
}
.nav-selection{
    display: grid;
    @include telefono() {
        grid-template-rows: repeat(3, 1fr);
    }
    @include tablet() {
        grid-template-columns: 1fr 3fr 1fr;
    }
}
.logo-dashboard{
    margin: 0 auto;
    width: 50%;
    @include tablet() {
        width: 60%;
    }
}
.shortcuts{
    @include telefono() {
        display: grid;
        grid-template-rows: repeat(5, 1fr);
        margin: 0 auto;
        top: 50%;
    }
    padding: 0rem 2rem;
    display: flex;
    justify-content: space-between;
    .btn-shortcut{
        margin: auto;
        height: 2rem;
        border: none;
        background-color: transparent;
        font-size: 1.7rem;
        font-weight: bold;
    }
    .btn-shortcut:hover{
        cursor: pointer;
    }
}

.option-client{
    border-radius: 1rem;
    height: 80vh;
    background-color: $principal;
    position: relative;
    .title-dashboard{
        color: azure;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    img{
        border-radius: 1rem;
        position: relative;
        height: 80vh;
        filter: opacity(60%);
    }
}
.option-client:hover{
    cursor: pointer;
}
.user-interaction{
    @include telefono() {
        gap: 10rem;
        .user-animation {
            img, button {
                height: 8rem;
            }
        }
        .options-animation {
            img {
                height: 5rem;
            }
        }
    }
    display: flex;
    justify-content: space-between;
    margin: auto;
    .user-animation{
        text-align: center;
        button{
            border: none;
            background-color: transparent;
            @include tablet() {
                width: 30%;
            }
        }
    }
    .options-animation{
        align-self: center;
        margin: 0;
        width: 40%;
        p{
            font-size: 1.7rem;
        }
        .window-options{
            // width: 40%;
            background-color: white;
            border-radius: 1rem;
            padding: 1rem 2rem;
            img{
                width: 40%
            }
            .option-menu{
                text-align: center;
                border-radius: 0.5rem;
            }
            .option-menu:hover{
                cursor: pointer;
            }
        }
    }
    .options-animation:hover{
        cursor: pointer;
    }
}
.btn-logout{
    border: none;
    background-color: transparent;
    button{
        width: 50%;
    }
}
.btn-logout:hover{
    cursor: pointer;
}
.actions{
     display: none;
}
.menu-buttons{
    position: absolute;
    // margin-right: 3rem;
    z-index: 100;
}