.client-branchs {
    display: grid;
    .title {
        display: flex;
        justify-content: center;
        background-color: $terciario;
        height: max-content;
        .title-p {
            top: 10%;
            text-align: center;
            position: absolute;
        }
        img{ 
            filter: opacity(60%);
            height: 20rem;
            width: 100%;
        }
    }
    .map {
        grid-row: 3;
        border-radius: 1rem;
        border-color: 3px $principal;
        margin: 0rem 2rem;
        height: 40rem;
    }
    @include tablet() {
        grid-template-columns: 25% 50% 25%;
        gap: 1rem;
        @include title-desktop();
        .map {
            grid-row: 1;
            grid-column: 2;
            top: 10%;
            height: 80vh;
        }
    }
}

.location{
    text-align: center;
    margin: auto;
    form{
        display: flex;
        flex-direction: column;
        select{
            padding: 1rem 2rem;
            border-color: $terciario;
            border-radius: 1rem;
            color: black;
            text-align: center;
            font-weight: bold;
        }
        button{
            margin-top: 2rem;
        }
    }
}