.services{
    display: grid;
    grid-template-columns: 2fr 6fr 2fr;
    height: 100vh;
}
.time-cost-service{
    margin: 5rem;
    display: grid;
    place-items: center;
    .title-service{
        font-size: 3rem;
        color: $principal;
        display: grid;
        place-self: start;
    }
    .circular-time{
        position: relative;
        width: 12rem;
        height: 12rem;
        border-radius: 50%;
        position: absolute;
        background: linear-gradient(90deg, $principal 50%, $terciario 50%);
        margin: auto;
        display: grid;
        place-items: center;
    }
    .circular-time::before{
        content: "";
        width: 12rem;
        height: 12rem;
        position: absolute;
        top: 0;
        left: 0;
        background: white content-box;
        border-radius: 50%;
        padding: 0.5em;
        box-sizing: border-box;
        font-size: 2rem;
    }
    .value-time{
        position: relative;
    }
}


