.reservation{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .calendar-time{
        display: grid;
        grid-template-columns: 6fr 4fr;
    }
    .time{
        margin: auto;
        p{
            font-weight: bold;
            margin-bottom: 10rem;
        }
    }
    .model-time{
        background-color: $gris;
        border-radius: 2rem;
    }
    .date-saved{
        text-align: center;
        margin-bottom: 3rem;
        h1{
            padding-top: 2rem;
            color: $principal;
            font-size: 2.4rem;
            margin: 0;
        }
        h4{
            margin: 0;
            font-size: 1.7rem;
        }
    }
    .select-time{
        padding: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .input-time{
            display: flex;
            flex-direction: column;
            input{
                width: 50%;
                margin: auto;
            }
        }
        button{
            margin: 0;
        }
    }
}