.product{
    display: grid;
    .title {
        display: flex;
        justify-content: center;
        background-color: $terciario;
        height: max-content;
        .title-p {
            top: 10%;
            text-align: center;
            position: absolute;
            color: azure;
        }
        img{ 
            filter: opacity(60%);
            height: 20rem;
            width: 100%;
        }
    }
    @include telefono() {
        @include grid-row(3, 1rem);
    }
    @include tablet() {
        grid-template-columns: 2fr 6fr 2fr;
        @include title-desktop();
    }
    height: 100vh;
}
.show-product{
    @include tablet() {
        margin-top: 5rem;
    }
    .container{
        width: 90%;
        margin: auto;
    }
    .header-products{
        display: flex;
        justify-content: space-between;
        h1,
        span{
            margin: 2rem 0;
            align-self: center;
        }
    }
    .product-description{
        display: grid;
        grid-template-columns: 3fr 7fr;
        gap: 2rem;
        p{
            text-align: justify;
        }
    }
    .price-product{
        text-align: end;
        color: $principal;
        font-size: 2rem;
        font-weight: bold;
    }
    .form-product{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 1rem;
        .input-product{
            display: flex;
            justify-content: center;
            margin-bottom: 2rem;
            input[type="number"]{
                -moz-appearance: textfield;
                text-align: center;
                border-color: $terciario;
                width: 10%;
            }
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button{
                -webkit-appearance: none;
                margin: 0;
            }
            .decrement,
            .increment{
                border: none;
                background-color: transparent;
                color: $terciario;
                font-size: 2.5rem;
                text-align: center;
                padding: 0.3rem 1rem;
            }
            .decrement:hover,
            .increment:hover{
                cursor: pointer;
            }
        }
    }
}

.more-products{
    background-color: black;
    color: white; 
    height: auto;
    overflow-y: scroll;
    p{
        color: white;
        font-size: 1.5rem;
        text-align: justify;
    }
    h4{
        font-weight: bold;
        font-size: 1.7rem;
    }
    .one-product{
        width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns: 3fr 7fr;
        gap: 2rem;
        .img-product{
            height: 90%;
            width: 90%;
            margin: auto;
        }
        .description-oneproduct{
            padding-right: 0.5rem;
        }
        .price-capacity{
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            p{
                font-weight: bold;
                font-size: 1.7rem;
            }
            span{
                font-weight: normal;
                font-size: 1.3rem;
            }
        }
    }
    .one-product:last-child{
        margin-bottom: 1rem;
    }
    .one-product:first-child{
        margin-top: 1rem;
    }
    .one-product:hover{
        cursor: pointer;
        @include degraded-background($morado-intenso, $morado-no-intenso);
    }
}