h1,
h2,
h3 {
  font-family: "Courgette", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

label {
  font-size: "Ubuntu", sans-serif;
}

.container {
  margin: auto;
  width: 80%;
}

.container-small {
  margin: auto;
  width: 90%;
}
@media (min-width: 600px) {
  .container-small {
    width: 75%;
  }
}
@media (min-width: 1024px) {
  .container-small {
    width: 50%;
  }
}

.center {
  text-align: center;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
  margin: 2rem;
}

.text-white {
  color: white;
}

.text-black {
  color: black;
}

.text-primary {
  color: #7218C6;
}

.width-100 {
  width: 100%;
}

.formulario form {
  width: 100%;
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
}
.formulario label {
  margin-bottom: 15px;
  position: relative;
  border-bottom: 1px solid #ddd;
}
.formulario input {
  width: 100%;
  padding: 10px 0px;
  margin-top: 20px;
  border: none;
  border-radius: 0.5rem;
  outline: none;
}
.formulario select {
  width: 100%;
  padding: 10px 0px;
  margin-top: 20px;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  text-align: center;
}
.formulario input::placeholder {
  opacity: 0;
  border-radius: 0.5rem;
}
.formulario span {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(30px);
  font-size: 0.825em;
  transition-duration: 300ms;
}
.formulario label:focus-within > span,
.formulario input:not(:placeholder-shown) + span {
  transform: translateY(0px);
}
.formulario label:focus-within > span,
.formulario select:not(:placeholder-shown) + span {
  transform: translateY(0px);
}

.btn-principal {
  font-weight: bold;
  border: none;
  background-color: #7218C6;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #F2E9E4;
  margin-bottom: 2rem;
}

.btn-principal:hover {
  cursor: pointer;
  background-color: #5d14a2;
}

.btn-secundario {
  font-weight: bold;
  border: none;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: #7218C6;
  padding: 1rem 2rem;
}

.btn-secundario:hover {
  cursor: pointer;
  background-color: #ebebeb;
}

.btn-terciario {
  font-weight: bold;
  border: none;
  background-color: #dcdcdc;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #7218C6;
  margin-bottom: 2rem;
}

.btn-terciario:hover {
  cursor: pointer;
  background-color: #c8c8c8;
}

.error {
  background-color: #ffbcbc;
  border-left: 5px solid #cb0000;
  text-align: center;
  padding: 0.2rem 0;
  margin: 1rem 2rem;
  font-weight: 700;
}

.successful {
  background-color: #b3ff90;
  border-left: 5px solid #329f00;
  padding: 0.2rem 0;
  text-align: center;
  margin: 1rem 2rem;
  font-weight: 700;
}

.btn-volver,
.btn-cerrar-sesion {
  border: none;
  background-color: #dcdcdc;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
}

.btn-volver:hover,
.btn-cerrar-sesion:hover {
  cursor: pointer;
  background-color: #c3c3c3;
}

.div-btn-volver {
  margin: 1rem;
}

.animacion-menu {
  display: flex;
  align-items: center;
}
.animacion-menu img {
  filter: invert(1);
  width: 4rem;
}
@media (min-width: 600px) {
  .animacion-menu {
    margin-right: 5rem;
    display: flex;
    justify-content: flex-end;
  }
  .animacion-menu img {
    filter: invert(1);
    width: 4rem;
  }
}
@media (min-width: 1024px) {
  .animacion-menu {
    margin-right: 5rem;
    display: flex;
    justify-content: flex-end;
  }
  .animacion-menu img {
    filter: invert(1);
    width: 4rem;
  }
}
.animacion-menu .acciones {
  display: none;
}

.animacion-menu:hover {
  cursor: pointer;
}

.mostrar {
  display: block;
}

.solo-icono {
  margin: 1rem 2rem 0rem 2rem;
}
.solo-icono a {
  width: 5rem;
}
.solo-icono a img {
  width: 5rem;
}
.solo-icono button {
  width: 5rem;
  background-color: transparent;
}

.opciones-menu {
  margin-top: 2rem;
}

.opciones-menu:first-child {
  margin: 0;
}

.btn-navegacion-barra {
  background-color: transparent;
  border: none;
  width: 5rem;
}
.btn-navegacion-barra p {
  margin: 0;
  color: black;
  font-size: 1.5rem;
  font-weight: 600;
}

.btn-navegacion-barra:hover {
  cursor: pointer;
}

.login-img:hover {
  cursor: pointer;
}

.no-mostrar {
  display: none;
}

.ocultar {
  visibility: hidden;
}

.icon-back {
  margin: 2rem 2rem;
  width: 10%;
}

.icon-back:hover {
  cursor: pointer;
}

.btn-back {
  border: none;
  background-color: transparent;
}

.btn-back:hover {
  cursor: pointer;
}

.btn-duals {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}

.btn-option {
  margin-bottom: 2rem;
}

.swal2-popup {
  background: #ffffff;
}

.swal2-title {
  color: #000000;
}

swal2-styled.swal2-confirm {
  background-color: #5d14a2;
}

.title {
  background-color: #C933C7;
}
.title .title-p {
  top: 50%;
  position: absolute;
  text-align: center;
  width: 20%;
}
.title img {
  position: relative;
  height: 100vh;
  width: 100%;
  filter: opacity(60%);
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #dcdcdc;
}
.dark-mode .ventana h1,
.dark-mode .ventana-registro h1 {
  color: #013440;
}
.dark-mode .seleccionar-fecha {
  color: #F2E9E4;
}

.container-left-primary {
  background: linear-gradient(#7218C6, #9623C8);
  display: grid;
}
.container-left-primary h1 {
  font-weight: bold;
  font-size: 5rem;
}
.container-left-primary h3 {
  font-size: 3rem;
  font-weight: lighter;
}
.container-left-primary .img-container-left {
  grid-row: 1;
  height: max-content;
}
.container-left-primary .container {
  margin-top: 5rem;
  padding-bottom: 100cqmin;
}
@media (min-width: 600px) {
  .container-left-primary {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
  }
  .container-left-primary .img-container-left {
    grid-column: 2;
    height: max-content;
  }
  .container-left-primary .container {
    margin: auto;
    padding: 0;
  }
}

.container-left-secondary {
  background: linear-gradient(#9623C8, #C933C7);
  display: grid;
  gap: 0rem;
  overflow-y: scroll;
  height: 100vh;
}
.container-left-secondary h1 {
  font-weight: bold;
  font-size: 5rem;
}
.container-left-secondary h3 {
  font-size: 3rem;
  font-weight: lighter;
}
.container-left-secondary .img-container-left {
  grid-row: 1;
  height: max-content;
}
.container-left-secondary .window-login {
  margin-top: 1rem;
}
@media (min-width: 600px) {
  .container-left-secondary {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .container-left-secondary .img-container-left {
    grid-column: 2;
  }
  .container-left-secondary .window-login {
    margin: auto;
  }
}

.container-left-secondary-register {
  background: linear-gradient(#9623C8, #C933C7);
  display: grid;
  gap: 0rem;
}
.container-left-secondary-register h1 {
  font-weight: bold;
  font-size: 5rem;
}
.container-left-secondary-register h3 {
  font-size: 3rem;
